.halaman-main{
    height: 55px;
}
.halaman-img{
    width: 100%;
    height: 300px;
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: end;
    justify-content: left;
}
.halaman-img:hover{
    transition: 0.3s;
    filter: brightness(80%);
}
.halaman-img>div{
    padding-top: 15px;
    height: 50px;
    text-decoration: none;
    width: 50px;
    color: aliceblue;
    font-size: large;
}
.halaman-title{
    text-align: left;
    padding: 10px;
    font-weight: 500;
    color: #555;
}
.detail-atas{
    display: flex;
}
.halaman-like{
    position: absolute;
    max-width: 1020px;
    width: 100%;
    padding: 10px;
    padding-right: 35px;
    display: flex;
    justify-content: right;
}
hr{
    margin: 5px;
}